body {
  background-color: #f5f5f5;
}

.app-wrapper {
  width: 1280px;
  overflow: hidden;
  margin: 0 auto;
  display: grid;
  grid-template-areas:
  "h h"
  "n m";
  grid-template-rows: 100px 1fr;
  grid-template-columns: 300px 10fr;
  column-gap: 30px;
}

.app-wrapper__content {
  grid-area: m;
  background: red;
}