*{
    padding: 0;
    margin: 0;
    border: 0;
}

.nav {
    grid-area: n;
    background: transparent;
}

.item {
    margin: 0 0 20px 0;
}

.svg {
    width: 30px;
    height: 30px;
}



.item a {
    display: flex;
    align-items: center;
    border-radius: 10px;
    gap: 15px;
    color: black;
    font-size: 20px;
    padding: 10px;
    text-decoration: none;
    opacity: 0.7;
    transition: all 0.3s;
}

.item a.active{
    opacity: 1;
    background: #fff;
    /*filter: drop-shadow(1px 5px 5px rgba(0, 0, 0, 0.2));*/
    box-shadow: 0.5px 2px 3px rgba(0, 0, 0, 0.1);
}